import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { GotHttpService } from '../got-http-service.service';
import {BrowserModule, DomSanitizer} from '@angular/platform-browser';

@Component({
  selector: 'app-beerview',
  templateUrl: './beerview.component.html',
  styleUrls: ['./beerview.component.css']
})
export class BeerviewComponent implements OnInit {
  allData: any;
  backgroundImg: any;
  nameData: any[] = [];
  constructor(private sanitizer: DomSanitizer, private _route: ActivatedRoute, private router: Router, private gotHttpService: GotHttpService , private location: Location) {
    this.backgroundImg = sanitizer.bypassSecurityTrustStyle('url(https://www.thebottlepeople.co.uk/image/cache/catalog/Glass%20Bottles/187ml%20Bordeaux%20bottle%20BP-1050x1200.jpg)');
   }
  ngOnInit() {
    let myName = this._route.snapshot.paramMap.get('name');
    console.log(myName);
    // this.currentBlog = this.blogService.getSingleBlog(myBlogId);
    this.gotHttpService.getBeers().subscribe(

      data => {
        console.log(data);
        this.allData = data;
        console.log(this.allData);
        for (let i of this.allData) {
if (i.name === myName) {
this.nameData.push(i);
}
        }
console.log(this.nameData);
   },
      error => {
        console.log("some error occured");
        console.log(error.errorMessage)
      }
    );
  }
  goBackToPreviousPage(): any {
        this.location.back();
      }
    
}






