import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';
import {RouterModule , Routes} from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { GotHttpService } from './got-http-service.service';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import {ToastModule} from 'ng2-toastr/ng2-toastr';
import { BeerviewComponent } from './beerview/beerview.component';
import { FilterPipe } from './filter.pipe';
import { StyleviewComponent } from './styleview/styleview.component';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AboutComponent,
    NotFoundComponent,
    BeerviewComponent,
    FilterPipe,
    StyleviewComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    FormsModule,
    ToastModule.forRoot(),
    RouterModule.forRoot([
      {path: 'home', component: HomeComponent},
      {path: '', redirectTo: 'home', pathMatch: 'full'},
      {path: 'beerview/:name', component: BeerviewComponent},
      {path: 'styleview', component: StyleviewComponent},
      {path: 'about', component: AboutComponent},
      {path: '**', component: NotFoundComponent},
    ])
  ],
  providers: [GotHttpService],
  bootstrap: [AppComponent]
})
export class AppModule { }




