import { Component, OnInit ,ViewContainerRef} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { GotHttpService } from '../got-http-service.service';
import { Pipe, PipeTransform } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';

@Component({
  selector: 'app-styleview',
  templateUrl: './styleview.component.html',
  styleUrls: ['./styleview.component.css']
})
export class StyleviewComponent implements OnInit {
allBeers: any;
finalStyleData: any;
  constructor(private _route: ActivatedRoute, private router: Router, private gotHttpService: GotHttpService, private toastr: ToastsManager, vcr: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vcr);
  }
  ngOnInit() {
    let myName = this._route.snapshot.paramMap.get('styleName');
    console.log(myName);
    function onlyUnique(value, index, self) {
      return self.indexOf(value) === index;
  }
    this.gotHttpService.getBeers().subscribe(
              data => {

                this.allBeers = data;
                console.log(this.allBeers);
let finalData = [];
for (let item of this.allBeers) {
if (item.style === myName) {
finalData.push(item);
}
}
console.log(finalData);
this.finalStyleData = finalData;
              } ,
              error => {
                console.log("some error occured");
                console.log(error.errorMessage);
              }
            );






  }

}



