import { Component, OnInit , ViewContainerRef} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { GotHttpService } from '../got-http-service.service';
import { Pipe, PipeTransform } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: []
})
export class HomeComponent implements OnInit {

  allBeers: any;
finalStyle: any;
selected: any;
searchText: any;
someProperty: any;
  constructor(private _route: ActivatedRoute, private router: Router, private gotHttpService: GotHttpService, private toastr: ToastsManager, vcr: ViewContainerRef) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit() {
    function onlyUnique(value, index, self) {
      return self.indexOf(value) === index;
  }
    this.gotHttpService.getBeers().subscribe(
              data => {

                this.allBeers = data;
                console.log(this.allBeers);
                let styleName = [];
                for (let item of this.allBeers) {
                  styleName.push(item.style);
                  }
                let uniqueStyleName = styleName.filter( onlyUnique );
                console.log(uniqueStyleName);
                this.finalStyle = uniqueStyleName;
                this.finalStyle.sort();
              } ,
              error => {
                console.log("some error occured");
                console.log(error.errorMessage);
              }
            );






  }
  onSelect(val) {
    console.log(val);
    this.someProperty = val;
   // if (this.someProperty === 'books') {
    //  this.toastr.success('Redirecting to books', 'Success!');
 //   this.allData = this.allData.filter(x => x.value === val);
  //  console.log(this.allData);
 this.router.navigate(['/styleview', { styleName : val }]).then(() => {
  // do whatever you need after navigation succeeds
});
}
}



